@use 'hero';
@use './componentes/infos_bar';
@use './componentes/buttons';
@use './event';
@use './variaveis';


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    color: variaveis.$textColor;
    list-style: none;
}

.container{
    max-width: 960px;
    width: 100%;
    margin: 0 auto;
    

    @media (max-width:640px) {
        max-width: 80%;
    }

    @media (max-width:1024px) {
        max-width: 90%;
    }
}